<template>
    <div class="home">
        <!-- hero -->
        <HomeHero></HomeHero>

        <div class="mt-5 ml-4 mr-4">
            <div class="d-flex justify-content-between">
                <h1> Banks </h1>
            </div>  

            <b-table striped :items="banks" :fields="bankFields" responsive dark></b-table>
        </div>

        <div class="mt-5 ml-4 mr-4">
            <div class="d-flex justify-content-between">
                <h1> Branches </h1>
            </div>  

            <b-table striped :items="branches" :fields="branchFields" responsive dark></b-table>
        </div>

        <div class="mt-5 ml-4 mr-4">
            <div class="d-flex justify-content-between mb-2">
                <h1> Bank Accounts </h1>
                <b-button variant="primary" @click="openAddModal()"> Add Bank Account </b-button>
            </div>
            <b-table striped :items="accounts" :fields="accountFields" responsive dark ref="accountsTable">
                <template v-slot:cell(Actions)="data">
                    <div class="d-flex flex-column justify-content-center">
                        <b-button class="mb-2" variant="info" @click="openUpdateModal(data.item)"> Update </b-button>
                        <b-button variant="secondary" @click="deleteAccount(data.item.bankAcctNo,data.item.bankName)"> Delete </b-button>
                    </div>
                </template>
            </b-table>
        </div>

        <b-modal id="updateAccount" ref="updateAccount" hide-footer :title="this.status == -1 ? 'Update Bank Account' : 'Add Bank Account'">
            <b-form-group
            label="Account Number"
            >
                <b-form-input
                required
                v-validate="'required|decimal|max:20'"
                v-model="acctNo"
                name="acctNo"
                :state="errors.has('acctNo') ? false : null"
                aria-describedby="acctNo-error"
            ></b-form-input>

            <b-form-invalid-feedback id="acctNo-error">
                This is a required field. This field must also be numeric with at most 20 digits.
            </b-form-invalid-feedback>
            </b-form-group>
            <b-form-group
            label="Bank Name"
            >
                <b-form-select
                required
                v-validate="'required'"
                v-model="bankName"
                name="bankName"
                :state="errors.has('bankName') ? false : null"
                aria-describedby="bankName-error"
                :options="bankNameOptions"
                @change="updateBranchOptions"
                ></b-form-select>

                <b-form-invalid-feedback id="bankName-error">
                    This is a required field.
                </b-form-invalid-feedback>
            </b-form-group>
            <b-form-group
            label="Branch Name"
            >
                <b-form-select
                required
                v-validate="'required'"
                v-model="branchName"
                name="branchName"
                :state="errors.has('branchName') ? false : null"
                aria-describedby="branchName-error"
                :options="this.branchNameOptions"
                ></b-form-select>

                <b-form-invalid-feedback id="branchName-error">
                    This is a required field.
                </b-form-invalid-feedback>
            </b-form-group>
            
            <div style="float: right">
                <b-button variant="primary" size="large" type="submit" @click="submitAccountForm()">Submit</b-button>
            </div>
        </b-modal>
    </div>
</template>

<script>
// @ is an alias to /src
import HomeHero from "@/pages/HomeComponents/HomeHero.vue";

export default {
  name: "home",
  components: { HomeHero },
  data() {
    return {
        workouts: null,
        workoutTitle: null,
        workoutLocation: null,
        workoutTimes: null,
        workoutMaterials: null,
        workoutDuration: null,
        banks: [],
        bankFields: [
          {
            key: 'bankName',
            label: 'Bank Name',
          },
          {
            key: 'bankMainAddress',
            label: 'Main Address',
          }
        ],
        branches: [],
        branchFields: [
            {
                key: 'bankBranchName',
                label: 'Branch Name'
            },
            {
                key: 'bankBranchAddress',
                label: 'Branch Address'
            },
            {
                key: 'bankName',
                label: 'Bank Name',
            }
        ],
        accounts: [],
        accountFields: [
            {
                key: 'bankAcctNo',
                label: 'Account Number'
            },
            {
                key: 'bankName',
                label: 'Bank Name'
            },
            {
                key: 'bankMainAddress',
                label: 'Bank Main Address'
            },
            {
                key: 'bankBranchName',
                label: 'Banch Name'
            },
            {
                key: 'bankBranchAddress',
                label: 'Banch Address'
            },
            'Actions'
        ],
        acctNo: null,
        bankName: null,
        branchName: null,
        currentAcctNo: null,
        currentbankName: null,
        bankNameOptions: [{value: null, text: "Select a Bank", disabled: true}],
        branchNameOptions: [{}],
        status: 0, //0 = default, -1 = updating, 1 = adding
    };
  },
  methods: {
      gotoWorkout(path,id){
          this.$router.push('/' + path + '/' + id)
      },
      addWorkout(){
        this.$validator.validateAll().then(result => {
            if (result) {
                var lastId = this.workouts[this.workouts.length-1].id;
                var workout = {
                    id: lastId+1,
                    title: this.workoutTitle,
                    location: this.workoutLocation,
                    duration: this.workoutDuration,
                    times: this.workoutTimes,
                    materials: this.workoutMaterials,
                    image: 'workout2.jpg',
                    reviews: [],
                    approved: this.$store.state.admin
                }
                this.$store.dispatch("create", workout);
                this.workoutTitle = null;
                this.workoutLocation = null;
                this.workoutTimes = null;
                this.workoutMaterials = null;
                this.workoutDuration = null;
                this.$refs.addWorkout.hide()
            } else {
            }
        }
    )
    },
    addWorkoutSuggestion(){
        this.$validator.validateAll().then(result => {
            if (result) {
                var lastId = this.workouts[this.workouts.length-1].id;
                var workout = {
                    id: lastId+1,
                    title: this.workoutTitle,
                    location: this.workoutLocation,
                    duration: this.workoutDuration,
                    times: this.workoutTimes,
                    materials: this.workoutMaterials,
                    image: 'workout2.jpg',
                    reviews: [],
                    approved: false
                }
                this.$store.dispatch("create", workout);
                this.workoutTitle = null;
                this.workoutLocation = null;
                this.workoutTimes = null;
                this.workoutMaterials = null;
                this.workoutDuration = null;
                this.$refs.addWorkout.hide()
            } else {
                }
        }
    )
    },
    async deleteAccount(bankAcctNo,bankName) {
      let loader = this.$loading.show({
          container: null,
      });
      var bankAccount = {
          "bankAcctNo": bankAcctNo,
          "bankName": bankName
      }  
      await this.$http.post('/bankaccount/delete',bankAccount).then(async (response)=>
        await this.$http.get('/bankaccount').then((response1)=>{this.accounts=response1.data})
      );
      loader.hide()

    },
    updateBranchOptions() {
        this.branchNameOptions = [{value: null, text: "Select a Branch", disabled: true}]
        var clearCurrentBranch = true
        for (var i = 0; i < this.branches.length; i++) {
          var temp = this.branches[i];
          if(temp.bankName == this.bankName) {
            if (temp.bankBranchName == this.branchName) {
                clearCurrentBranch = false
            }
            var obj = {
              value: temp.bankBranchName,
              text: temp.bankBranchName
            };
            this.branchNameOptions.push(obj);
          }
        }
        if (clearCurrentBranch) {
            this.branchName = null
        }
        if (this.branchNameOptions.length == 1 && this.bankName) {
            this.branchNameOptions = [
                {value: null, text: "Sorry. No available branches."}
            ]
        }
    },
    openAddModal() {
        this.currentAcctNo = null
        this.currentbankName = null
        this.acctNo = null
        this.bankName = null
        this.branchName = null
        this.updateBranchOptions()
        this.status = 1
        this.$refs.updateAccount.show()
    },
    openUpdateModal(bankAcct) {
        this.currentAcctNo = bankAcct.bankAcctNo
        this.currentbankName = bankAcct.bankName
        this.acctNo = bankAcct.bankAcctNo
        this.bankName = bankAcct.bankName
        this.branchName = bankAcct.bankBranchName
        this.updateBranchOptions()
        this.status = -1
        this.$refs.updateAccount.show()
    },
    async updateAccountRequest(bankAccount) {
        let loader = this.$loading.show({
            container: null,
        });
        await this.$http.put('/bankaccount/update',bankAccount).then(async (response)=>{
            this.status = 0
            await this.$http.get('/bankaccount').then((response1)=>{this.accounts=response1.data})
        })
        loader.hide()
    },
    async createAccountRequest(bankAccount) {
        let loader = this.$loading.show({
            container: null,
        });
        await this.$http.post('/bankaccount/create',bankAccount).then(async (response)=>{
            this.status = 0
            await this.$http.get('/bankaccount').then((response1)=>{this.accounts=response1.data})
        })
        loader.hide()
    },
    async submitAccountForm() {
        await this.$validator.validateAll().then(async result => {
            if (result) {
                this.$refs.updateAccount.hide()
                if (this.status == -1) {
                    var bankAccount = {
                        bankAcctNo: this.acctNo,
                        bankName: this.bankName,
                        bankBranchName: this.branchName,
                        currentBankAcctNo: this.currentAcctNo,
                        currentBankName: this.currentbankName,
                    }
                    await this.updateAccountRequest(bankAccount)
                }
                else if (this.status == 1) {
                    var bankAccount = {
                        bankAcctNo: this.acctNo,
                        bankName: this.bankName,
                        bankBranchName: this.branchName,
                    }
                    await this.createAccountRequest(bankAccount)
                }
            }
        })
    },
    fetchAll() {
      this.$http.get('/bank').then((response)=>{
          this.banks=response.data
          for (var i = 0; i < response.data.length; i++) {
          var temp = response.data[i];
          var obj = {
              value: temp.bankName,
              text: temp.bankName
          };
          this.bankNameOptions.push(obj);
          }
      })
      this.$http.get('/branch').then((response)=>{this.branches=response.data})
      this.$http.get('/bankaccount').then((response)=>{this.accounts=response.data})
    }
  },
  async created() {
      let loader = this.$loading.show({
        container: null,
      });
      await this.fetchAll()
      loader.hide()
  }
};
</script>
<style>
